import React,{ Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Skycons from 'react-skycons'
import html2canvas from 'html2canvas';
import CancelIcon from '@material-ui/icons/Cancel';
import { Bar, Line, horizontalBar } from 'react-chartjs-2';
import Paper from '@material-ui/core/Paper';

import Background from './asset/icon-bg-4.gif';
import Background1 from './asset/icon-bg-5.gif';





const theme ={
    container:{
        fontFamily:` 'Ubuntu', sans-serif`,      
        display: 'flex',
        justifyContent: 'space-evenly',
        flexDirection: 'row',
        alignContent: 'flex-start',
        flexWrap:'wrap',
        scrollBehavior: 'smooth',    
        width:'auto',
        top:'64px',
        left:'0px',
        right:'0px',
        bottom:'56px',
        // height:'calc(100vh - 120px)',
        margin:'15px 35px 15px 35px',
        borderRadius:'70px', 
        boxShadow:'inset  0 0 15px rgba(0, 0, 0, 0.322)',
        position:'absolute',
        overflow:'auto',
        backgroundImage: `url(${Background})`,
        backgroundSize:'contain',
        backgroundPosition:'center',
        scrollBehavior: 'smooth',
        // scrollbarColor: 'rebeccapurple green',
        // scrollbarWidth: 'thin',
        scrollbarHeight: '8px',
    },
    // inner:{

    // },


    cardContainer:{
        margin:'20px',
        order:'1',
        // flexShrink: '2',

        // backgroundImage: 'linear-gradient(rgb(47,150,163),rgb(48,62,143))',
        
        backgroundSize: 'cover',
        backgroundRepeat:' no-repeat',
        backgroundPosition: 'center',

        
        // zIndex:'-1',
        // width:'400px',
        height:'500px',
        display: 'flex',
        flexDirection:'column',
        justifyContent: 'center',
        alignItems: 'center', 
        color: 'white',
        borderRadius:'50px',
        boxShadow:' 0 0 15px rgba(0, 0, 0, 0.322)',
        transition: 'background 2s',
        WebkitTransition: 'background 2s',
        OTransition: 'background 2s',
        MozTransition: 'background 2s',
        background:'linear-gradient(rgb(47,150,163),rgb(48,62,143))'

    },
    location:{
        order: '1',
        flexDirection: 'row',
        height: '30vh',
        width: '80%',
        display: 'flex',
        
        justifyContent: 'space-around',
        alignItems: 'center',  
    },
    locationTimezone:{
        textShadow:'0px 0px 5px #707070',
    },
    temperature:{
        order: '2',
        flexDirection: 'column',
        height: '30vh',
        width: '80%',
        display: 'flex',
        
        justifyContent: 'space-around',
        alignItems: 'center',
    },
    chartContainer:{
        order: '3',
        height: '22vh',
        width: '80%',
        background:'linear-gradient(rgba(255, 255, 255, 0.6),rgba(255, 255, 255, 1))',
        paddingBottom:'20px',
        margin:'20px'
    },

    degreeSection:{
        height: '10vh',
        display: 'flex',
        alignItems: 'center',
        cursor: 'pointer',
    },
    temperatureDescription:{
        height: '10vh',
        width: '60%',
        fontSize:'30px',
        display: 'flex',
        justifyContent: 'space-evenly',
        flexDirection: 'row',
        textShadow:'0px 0px 5px #707070',
    },
    icon:{
        
        width:'180px',
        zIndex:'1',
        backgroundBlendMode:'luminosity',

    },
    btn:{
        display:'inline-block',
        color:'#ededed',
        backgroundColor:'#6d8fe0',
       '&hover':{
            backgroundColor:'#303f9f',
        }
    },
    canvas:{
        display:'flex',
        flexDirection:'column',
        justifyContent:'center',
        alignItems:'center',
        width:'100%',
        height:'100vh',
    },
    button:{
        backgroundColor:'#6d8fe0',    
        '&:hover': {
            backgroundColor:'#3444a3',
        },
        
        height:'30px',
        width:'80px',
        borderRadius:'5px',
        margin:'20px',
        textAlign:'center',
        color:'white',
        
    },


}


class WeatherCard extends Component{
    
constructor(){
    super()
    this.state={
        anchorEl:null,
        saveMode:false,
        menuIndex:'',
    }
}
render(){
    const {classes} = this.props
    
    return(
        <div>
        {this.state.saveMode?null:
        
            <div className={classes.container} id='container' style={{backgroundImage: this.props.loadingStatus ? `url(${Background1})` : ``,WebkitScrollbar:'width(120px) height(20px)'}}>
                
            {this.props.weather!==undefined?this.props.weather.map((item,index)=>{
                return(
                   
                <div key={index} id={index} className={classes.cardContainer} style={{background:`url(`+`${item.image}`+`)`}} >
                    
                    <Button aria-controls="simple-menu" aria-haspopup="true" className={classes.btn} onClick={(e)=>this.handleClick(e,index)}>
                        Menu   
                    </Button>
                    <Menu
                        
                        id="simple-menu"
                        anchorEl={this.state.anchorEl}
                        keepMounted
                        open={Boolean(this.state.anchorEl)}
                        onClose={(e)=>this.handleClose(e)}
                        >
                        <MenuItem onClick={(e)=>this.menuHours(e)}>Next 24 Hours</MenuItem>
                        <MenuItem onClick={(e)=>this.menuSave(e)}>Save picture</MenuItem>
                        <MenuItem onClick={(e)=>this.menuDelete(e)}>Delete</MenuItem>
                    </Menu>
                    <div className={classes.location}>
                        <h1 className={classes.locationTimezone}>{item.locationTimezone}</h1>
                        <div className={classes.icon}>
                            <Skycons
                                color='white'
                                icon={item.icon}
                                autoplay={true}
                            />
                        </div>
                    </div>
                    {!item.showChart?
                        <div className={classes.temperature}>
                            <div onClick={(e)=>this.changeF(e,index)} className={classes.degreeSection}>
                                <h2 className="temperature-degree" style={{textShadow:'0px 0px 5px #707070',}}>{item.temperatureDegree}</h2>
                                <span style={{textShadow:'0px 0px 5px #707070',}}>{item.temperatureSpan}</span>
                            </div>
                            
                            <div className={classes.temperatureDescription }>{item.temperatureDescription}</div>
                        </div>
                        :
                        
                        <Paper   className={classes.chartContainer}>
                            <CancelIcon style={{right:'80px'}} color="primary" onClick={(e)=>this.closeChart(index)}/>
                            <Line
                                data={item.chartData}
                                width={80}
                                height={20}
                                options={{ maintainAspectRatio: false }}
                            />
                        </Paper>
                    }
                    
                </div>
               )
            }):null}
                
            </div>
         }
            <div id='body' className={classes.canvas}>
                {this.state.saveMode?
                    <div style={{display:'flex',justifyContent:'center'}}>
                        {/* <a className={classes.button} href="/images/image-name.jpg" download>SAVE</a> */}

                        <a id="download" download="weatherCard.png" onClick={this.download}>
                            <button className={classes.button}  >SAVE</button>
                        </a>

                        {/* <Button onClick={this.save} className={classes.button} variant="contained" color="primary">Save</Button> */}
                        {/* <Button className={classes.button} onClick={this.tweetCurrentPage} variant="contained" color="primary">SHARE</Button> */}
                        <a id="share"  data-pgaction-redirection="0"  
                            // onClick={this.tweetCurrentPage}
                            onClick={this.share}
                            target="_blank" alt="facebook">
                            <button className={classes.button}   >SHARE</button>
                        </a>
                        <CancelIcon style={{margin:'20px'}} color="primary" onClick={this.closeSaveMode}/>
                    </div>:null
                    
                }
            </div>

        </div>
    )     
}

download() {
    var download = document.getElementById("download");
    var image = document.getElementById("myCanvas").toDataURL("image/png")
        .replace("image/png", "image/octet-stream");
    download.setAttribute("href", image);
    }
share() {
    // var image = document.getElementById("myCanvas").toDataURL("image/png").replace("image/png", "image/octet-stream");

    console.log(document.title)
    window.open("https://twitter.com/share?url="+ encodeURIComponent(window.location.href)+"&text="+document.title, '', 'menubar=no,toolbar=no,resizable=yes,scrollbars=yes,height=300,width=600');
    return false;
    }

componentDidUpdate(prevProps) {
    if (this.props.weatherNum > prevProps.weatherNum) {
        var elmnt = document.getElementById("container");
        elmnt.scrollTop = 50000;
        console.log('scroll')
    }
  }
  

closeChart=(index)=>{
    console.log('closeChart'+index)
    this.props.closeChart(index)
}

changeF=(e,index)=>{
    console.log('changeF'+index)
    this.props.changeF(index)
}    

handleClick=(e,index)=>{
    console.log(index)
    this.setState({
        menuIndex:index,
        anchorEl:e.target
    });
}
handleClose = (e,index) => {
    console.log(index)
    this.setState({
        anchorEl:null,
        menuIndex:''
    });
}
menuHours=(e) =>{
    console.log('openchart'+this.state.menuIndex)
    this.props.showChart(this.state.menuIndex)
    this.setState({
        anchorEl:null,
        menuIndex:''
    });
}
menuDelete=(e) =>{
    console.log('delete'+this.state.menuIndex)
    this.props.delete(this.state.menuIndex)
    this.setState({
        anchorEl:null,
        menuIndex:''
    });
}
menuSave=(e)=>{
    this.setState({
        saveMode:true,
    })
    const b=document.getElementById('body')
    console.log(document.getElementById(this.state.menuIndex))

    html2canvas(document.getElementById(this.state.menuIndex), { letterRendering: 1, allowTaint : true,useCORS:true,}).then(function(canvas) {
        b.appendChild(canvas);
        canvas.crossOrigin = "anonymous"; 
    }).then(
    function(e) {
        const canvas = b.getElementsByTagName("canvas");
        canvas[0].setAttribute("id", "myCanvas")
        console.log(canvas[0]);
        canvas[0].style.borderRadius= '50px'
        canvas[0].style.boxShadow= '10px 10px 5px grey'
        
    
    }
    )
    this.setState({
        anchorEl:null,
        menuIndex:'',
    });
}
    

tweetCurrentPage=()=>{
    window.open("https://twitter.com/share?url="+ encodeURIComponent(window.location.href)+"&text="+document.title, '', 'menubar=no,toolbar=no,resizable=yes,scrollbars=yes,height=300,width=600');
    return false;
}
closeSaveMode=()=>{
    const b=document.getElementById('body')
    const canvas = b.getElementsByTagName("canvas")
    b.removeChild(canvas[0])
    this.setState({saveMode:false})

}



}
export default withStyles(theme)(WeatherCard);