class App extends Component{


    constructor(){
      super()
      this.state={
        weather:[],
        locationTimezone:'',
        image:'',
        temperatureDegree:'',
        temperatureDescription:'',
        temperatureSpan:"°F",
        icon:'',
        value:'',
        commentOpen:false,
        thumbsOpen:false,
        chartData:'',
        loadingStatus:false,
        weatherNum:0,
      }
    }
  
 

    
    render(){
      const {classes}=this.props


    return (
   
      <div >
        <SearchAppBar 
          search={this.search}
          showLocal={this.showLocal}
          weather={this.state.weather}

          />
        <WeatherCard
        weather={this.state.weather}
        delete={this.delete}
        save={this.save}
        locationTimezone={this.state.locationTimezone}
        image={this.state.image}
        temperatureDegree={this.state.temperatureDegree}
        temperatureDescription={this.state.temperatureDescription}
        temperatureSpan={this.state.temperatureSpan}
        changeF={this.changeF}
        closeChart={this.closeChart}
        showChart={this.showChart}
        loadingStatus={this.state.loadingStatus}
        weatherNum={this.state.weatherNum}
         />
        
        <div className={classes.footer} color='primary'>
        <BottomNavigation value={this.state.value} onChange={this.handleChange}  className={classes.root}>
          <BottomNavigationAction className={classes.navIcon} color="primary" onClick={this.handleThumbsOpen} label="Thumbs Up!" value="Thumbs" icon={<ThumbUpIcon />} />
          <BottomNavigationAction className={classes.navIcon} onClick={this.handleCommentOpen} label="Leave Comment" value="Comment" icon={<TextsmsIcon />} />
          <BottomNavigationAction className={classes.navIcon} href="mailto:huangying0528@gmail.com?Subject=Hello--%20from%20weather%20App%20user" label="Send Email" value="Email" icon={<EmailIcon />} />
        </BottomNavigation>
        </div>

        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          className={classes.modal}
          open={this.state.thumbsOpen}
          onClose={this.handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
          }}
        >
          
          <Fade in={this.state.thumbsOpen}>
            <div className={classes.paper}>
              <h2 className={classes.title} id="transition-modal-title">Thank you!</h2>
              <p className={classes.content} id="transition-modal-description"></p>
            </div>
          </Fade>
        </Modal>

        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          className={classes.modal}
          open={this.state.commentOpen}
          onClose={this.handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
          }}
        >
          
          <Fade in={this.state.commentOpen}>
            <div className={classes.paper}>
              <h2 className={classes.title} id="transition-modal-title">Leave your comment here:</h2>
              <textarea className={classes.textarea} rows="2" cols="24" />
              <Button onClick={this.handleClose} color='primary'>Submit</Button>
            </div>
          </Fade>
        </Modal>
      </div>
    );}
    
    handleThumbsOpen = () => {

          //console.log('ThumbsOpen')
          this.setState({
            thumbsOpen:true,
            commentOpen:false,
          })
    }
    handleCommentOpen = () => {
          //console.log('CommentOpen')
          this.setState({
            thumbsOpen:false,
            commentOpen:true,
          })
    }

  
     handleClose = () => {
      this.setState({
        thumbsOpen:false,
        commentOpen:false,
      })
    };

    handleChange = (event, newValue) => {
      this.setState({
        value:newValue
      })
    };

    componentWillMount=()=>{
      //console.log("run default");
      this.showLocal()
      
    }

    showLocal=()=>{
      
      // get24hours();
      if(navigator.geolocation){ 
      navigator.geolocation.getCurrentPosition(position=>{   
          const long = position.coords.longitude;
          const lat = position.coords.latitude;
          this.getToGoogleMap(lat,long);
      }); 
      }
      else{
          alert("can't get geolocation");
      }
    }
    

    getToGoogleMap=(lat,lon)=>{

      //console.log(lat, lon);

      const proxy = 'http://cors-anywhere.herokuapp.com/';
      const api = `https://google-maps-geocoding.p.rapidapi.com/geocode/json?latlng=${lat}%2C${lon}&language=en`;
      
      fetch(api, {
        "method": "GET",
        "headers": {
          "x-rapidapi-key": process.env.REACT_APP_API_KEY,
          "x-rapidapi-host": "google-maps-geocoding.p.rapidapi.com"
        }})
      .then(response=>{
          
          return response.json();
      })
      .then(data=>{
          const {compound_code} = data.plus_code;
          const pos = compound_code.indexOf(" ");
          const location = compound_code.slice(pos, compound_code.length);
          this.setState({
            locationTimezone:location,
            loadingStatus:true
          })
          this.showWeather(lat,lon,location);
      });
    }

    search = (input) =>{
      //console.log('App search')
      
        this.getGeo(input);
        
        
    }
    getGeo=(words)=>{
      const cityName =  words.replace(/\s+/g, '+');
      const proxy =  'http://cors-anywhere.herokuapp.com/';
      const api =  `https://maps.googleapis.com/maps/api/geocode/json?address=${cityName}&key=${process.env.REACT_APP_GOOGLE_API_KEY}`;
      fetch(api)
      .then(response=>{
          return response.json();
      })
      .then(data=>{
  
          
          if(data.results[0]){
            const{lat,lng}=data.results[0].geometry.location;
            const location =data.results[0].formatted_address
            this.showWeather(lat,lng,location)
            this.setState({
              locationTimezone:location,
              loadingStatus:true
            })
          }else{
            alert('Cannot find location, please try again.')
          }
          
          
      });
  }
  showWeather=(lat,long,location0)=>{
    
      var d = new Date();
      var hours=[]
      var i
      for(i=0;i<24;i++){
          var hour = i+d.getHours();
           if(d.getMinutes()<15){
              var minString = "00";  
          }else if(d.getMinutes()>=15&&d.getMinutes()<45){
              var minString = "30";
          }else if(d.getMinutes()>=45){
              var minString = "00";  
              hour=hour+1;
          }
          
          if(hour>=24){
              hour = hour-24;
          }
          else{ hour=hour;}
  
      
          if(hour<10){
              var hourString = "0"+hour.toString();
          
              
          }else{
              var hourString = hour.toString();
          }
         
          hours[i] = Number(hourString)+":"+Number(minString);
          
      }
      
  
      //console.log(lat, long);
    const proxy =  'http://cors-anywhere.herokuapp.com/';
    const api =  `https://dark-sky.p.rapidapi.com/${lat},${long}?lang=en&units=auto`;

    fetch(api, {
      "method": "GET",
      "headers": {
        "x-rapidapi-key": process.env.REACT_APP_API_KEY,
        "x-rapidapi-host": "dark-sky.p.rapidapi.com"
      }
    })
    .then(response => {
      //console.log(response);
      return response.json();
    }).then( data=> {
        
        const {temperature,summary,icon} =data.currently;
        //console.log(data)
        var i
        var hourlyData=[]
        for(i=0;i<24;i++){
         hourlyData[i] = data.hourly.data[i].temperature;
        }
        //console.log(hourlyData)
        const pos = location0.indexOf(",");
        const location = location0.slice(0,pos)
        
        this.setIcon(icon)
        //set Dom elements from the api
        this.getImg(location+' city')
        // this.get24hoursTemp(hourlyData)
        // this.setIcons(icon,document.querySelector('.icon'))

        this.setState({
          temperatureDegree:temperature,
          temperatureDescription:summary,
          temperatureSpan:"°F",
          chartData:{
            labels: hours,
            datasets: [{
                label: 'Temperature',
                data: hourlyData,
                backgroundColor: 
                  ['rgba(54, 162, 235, 0.2)',],
                borderColor: 
                  ['rgba(255, 99, 132, 1)',],
            }]
          },
          
          
        })

             
        
    });
  }

  setIcon=(icon)=>{

    const currentIcon = icon.replace(/-/g,"_").toUpperCase();
    this.setState({
      icon:currentIcon
    })
  } 

 getImg=(keyword)=>{

    var num = 10; 
    var color = 'blue'; 
    // &color=${color}  

    const proxy = 'http://cors-anywhere.herokuapp.com/';
    const api =`https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/Search/ImageSearchAPI?q=${keyword}%20city%20birdview&pageNumber=1&pageSize=${num}&autoCorrect=true`;
    // orientation=landscape
    
    fetch(api,
      {
        "method": "GET",
        "headers": {
          "x-rapidapi-key": process.env.REACT_APP_API_KEY,
          "x-rapidapi-host": "contextualwebsearch-websearch-v1.p.rapidapi.com"
        }
      }
      )
    .then(response=>{
        return response.json();
    })
    .then(data=>{

        const num = Math.floor(Math.random() * 10)
        //console.log(num)
        const img = data.value!==undefined?data.value[num].url:null;
        this.setState({image:img})
        const W=this.state.weather
        const number = W.length+1
        W.push({
            locationTimezone:this.state.locationTimezone,
            image:this.state.image,
            temperatureDegree:this.state.temperatureDegree,
            temperatureDescription:this.state.temperatureDescription,
            temperatureSpan:this.state.temperatureSpan,
            icon:this.state.icon,
            chartData:this.state.chartData,
            showChart:false

        })
        this.setState({
          weather:W,
          loadingStatus:false,
          weatherNum: number
        //   [{
        //     locationTimezone:this.state.locationTimezone,
        //     image:this.state.image,
        //     temperatureDegree:this.state.temperatureDegree,
        //     temperatureDescription:this.state.temperatureDescription,
        //     temperatureSpan:this.state.temperatureSpan,

        // }]
        })
        //console.log(this.state.weather,this.state.weatherNum)
    })
    
  }

  changeF=(index)=>{
    const temperature = this.state.weather[index].temperatureDegree
        if(this.state.weather[index].temperatureSpan==="°F"){

           const celcius = (temperature - 32) * (5/9);
           const weather0=this.state.weather
           weather0[index].temperatureDegree=+celcius.toFixed(2)
           weather0[index].temperatureSpan='°C'
           this.setState({
             weather:weather0
           })

        }else{
          const fahrenheit = 32+(temperature*(9/5));
          const weather0=this.state.weather
          weather0[index].temperatureDegree=+fahrenheit.toFixed(2)
          weather0[index].temperatureSpan='°F'
          this.setState({
            weather:weather0
          })
        }
  }
  delete=(index)=>{
    
    const W=this.state.weather
    const number = this.state.weatherNum-1
    W.splice(index, 1);
    this.setState({
      weather:W,
      weatherNum:number
    })
  }
  save=(index)=>{
    //console.log(index)
  }
  closeChart=(index)=>{
    //console.log(index)
    var w = this.state.weather
    w[index].showChart=false
    this.setState({
        weather:w
    })
}
showChart=(index)=>{
  var w = this.state.weather
  w[index].showChart=true
  this.setState({
      weather:w
  })
}


  
}
 
export default withStyles(theme)(App);