import React ,{ Component } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import { fade, withStyles } from '@material-ui/core/styles';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import Button from '@material-ui/core/Button';

const styles=theme =>( {
  root: {
    flexGrow: 1,
    fontFamily:` 'Ubuntu', sans-serif`, 
  },
  navBar:{
    display: 'flex',
    justifyContent: 'space-evenly',
    flexDirection: 'row',
    alignContent: 'flex-start',
    flexWrap:'wrap',    
    opacity: 0.5,
    height:'64px',
    top:'0px',
    width:'100%',
    position:'fixed',
    filter: 'blur(20px) contrast(2)',
    // background:`url('')`,
    ZIndex:-1,
  
  },

  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    fontFamily:` 'Ubuntu', sans-serif`,
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    
    boxShadow:'inset 0 0 5px #c4c5c8',
    '&:hover': {
      boxShadow: 'inset 0 0 3px #b2b2b2',
    },
     backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,

    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),

      width: 'auto',
    },
    [theme.breakpoints.up('md')]: {
      marginLeft: theme.spacing(1),
      marginRight:'0',
      width: '40%',
    },
  },
  searchIcon: {
    width: theme.spacing(7),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    
    padding: theme.spacing(1, 1, 1, 7),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: 320,
      '&:focus': {
        width: 400,
      },
    },
  },
  Btn:{
    height:'30px',
    width:'40px',
    left:'10px',
    // backgroundColor: fade(theme.palette.common.white, 0.05),
    backgroundColor:'#6d8fe0',
    [theme.breakpoints.up('sm')]: {
      width: 100,
      
    },
    [theme.breakpoints.up('lg')]: {
      width: 200,
      
    },

  },
  Button:{
    marginRight:'20px',
    height:'30px',
    width:'20px',
    left:'10px',
    backgroundColor:'#6d8fe0',
    // backgroundColor: fade(theme.palette.common.blue, 0.05),
    [theme.breakpoints.up('sm')]: {
      width: 100,
      
    },
    [theme.breakpoints.up('lg')]: {
      width: 200,
      
    },

  },
  container:{

    display: 'flex',

    justifyContent: 'space-evenly',
    flexDirection: 'row',
    alignContent: 'flex-start',
    flexWrap:'wrap',   
    width:'auto', 
    // // height:'100%-30',
    // margin:'0 0 15px 0',
    // borderRadius:'70px', 
    // boxShadow:'inset  0 0 15px rgba(0, 0, 0, 0.322)',
    position:'fixed',
    top:'0px',
    left:'0px',
    right:'0px',
    height:'64px',
    overflow:'hidden', 
    width:'100%',    
},
  cardContainer:{
    margin:'20',
    order:'1',
    // flexShrink: '2',
    backgroundSize: 'cover',
    backgroundRepeat:' no-repeat',
    backgroundPosition: 'center',
    transform: 'translateY(-200px) skewX(15deg)',
    // transform: 'translateY(-100px)',
    // zIndex:'-1',
    width:'30%',
    height:'500px',
    color: 'white',

},

});

class SearchAppBar extends Component{


  constructor(){
    super()
    this.state={
      searchInput:'',

    }

  }

  render() {
    const {classes} = this.props
    return(
    <div className={classes.root}>
      <AppBar color='transparent'>
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="primary"
            aria-label="open drawer"
          >
            {/* <MenuIcon /> */}
          </IconButton>
          <Typography className={classes.title} variant="h6" noWrap>
            How's the weather there
          </Typography>
          <Button onClick={(e)=>this.submitSearchLocal(e)} variant="contained" color="primary" value='Search' className={classes.Button} > 
              LOCAL
          </Button>
          <div className={classes.search}>

            <div className={classes.searchIcon}>
              <SearchIcon  className={classes.searchIcon} />
            </div>
            <InputBase
              placeholder="Search…"
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ 'aria-label': 'search' }}
              value={this.state.searchInput}
              onChange={(e)=>this.getInput(e)}
              onKeyUp={(e)=>this.handleKeyPress(e)}
            />
          </div>            
          <Button onClick={(e)=>this.submitSearch(e)} variant="contained" color="primary" value='Search' className={classes.Btn} > 
            SEARCH
          </Button>

        </Toolbar>
      </AppBar>

      <div className={classes.navBar}  style={{backgroundImage: this.props.weather[0]!==undefined? `${this.props.weather[0].image}`: `linear-gradient(45deg, #93a5cf 0%, #e4efe9 100%)` }} >

        <div className={classes.container}>


          {this.props.weather!==undefined?this.props.weather.map((item,index)=>{
                return(
                <div key={index} className={classes.cardContainer} style={{backgroundImage:`${item.image}`}}>

                </div>
                )
            }):null}

        </div>

      </div>

      


    </div>
  )};

  getInput = (e)=>{
    this.setState({
      searchInput:e.target.value
    })
  }

  submitSearch =()=> {
    const letter = this.state.searchInput.replace(/\s+/g, '');
    if(letter){
    this.props.search(this.state.searchInput)
    console.log(this.state.searchInput)
    }
    else console.log('input not exist')
    this.setState({
      searchInput:'',
    })
  }
  submitSearchLocal=()=>{
    this.props.showLocal()
  }
  handleKeyPress=(e)=>{
    
    if(e.keyCode===13){
      console.log('hello')
      this.submitSearch()
      
      return false;
      
  }
  }
}
export default withStyles(styles)(SearchAppBar);